
/**
 * @imports
 */
import _get from './get.js';

/**
 * Tells if the given path exists.
 *
 * @param object 				obj
 * @param array 				path
 * @param bool	 				detectObservables
 *
 * @return bool
 */
export default function(obj, path, detectObservables = true) {
	var reciever = {};
	_get(obj, path, reciever, detectObservables);
	return reciever.exists;
};
