
/**
 * @imports
 */
import _isObservable from '../js/isObservable.js';
import _isTypeObject from '../js/isTypeObject.js';
import _isFunction from '../js/isFunction.js';
import _arrFrom from '../arr/from.js';
import _get from './get.js';

/**
 * Sets a value to the given path.
 *
 * @param object 				obj
 * @param array 				path
 * @param mixed 				val
 * @param object|function 		buildTree
 * @param bool	 				detectObservables
 *
 * @return bool
 */
export default function(obj, path, val, buildTree = {}, detectObservables = true) {
	const _set = (target, key, val) => {
		var isObsvble = detectObservables && _isObservable(target);
		if (isObsvble) {
			target.set(key, val);
		} else {
			target[key] = val;
		}
	};
	path = _arrFrom(path);
	var target = obj;
	for(var i = 0; i < path.length; i ++) {
		if (i < path.length - 1) {
			if (!_isTypeObject(target) && !_isFunction(target)) {
				return false;
			}
			var branch = _get(target, path[i], {}/*reciever*/, detectObservables);
			if (!_isTypeObject(branch)) {
				if (!buildTree) {
					return false;
				}
				branch = _isFunction(buildTree) ? buildTree(i) : buildTree;
				_set(target, path[i], branch);
			}
			target = branch;
		} else {
			_set(target, path[i], val);
			return true;
		}
	}
};
