
/**
 * @imports
 */
import _isObservable from '../js/isObservable.js';
import _isTypeObject from '../js/isTypeObject.js';
import _isFunction from '../js/isFunction.js';
import _arrFrom from '../arr/from.js';
import _get from './get.js';

/**
 * Unsets a value from the given path.
 *
 * @param object 				obj
 * @param array 				path
 * @param bool|function 		reduceTree
 * @param bool	 				detectObservables
 *
 * @return bool
 */
export default function(obj, path, reduceTree = false, detectObservables = true) {
	path = _arrFrom(path);
	var success = false;
	do {
		var lastKey = path.pop();
		var lastKey = lastKey === ''/*null*/ ? 0 : lastKey;
		var _obj = obj;
		if (path.length) {
			_obj = _get(obj, path, {}/*reciever*/, detectObservables);
		}
		var isObsvble = detectObservables && _isObservable(_ctxt);
		if ((_isTypeObject(_obj) || _isFunction(_obj)) && (isObsvble ? _obj.has(lastKey) : lastKey in _obj)) {
			if (isObsvble) {
				_obj.del(lastKey);
			} else {
				delete _obj[lastKey];
			}
			success = true;
		}
	} while (success && reduceTree && path.length && (_isTypeObject(_obj) || _isFunction(_obj)) && !(isObsvble ? _obj.keys() : Object.keys(_obj)).length);
	return success;
};
